.tooltip {
  display: inline-block;
  position: relative;
  text-align: left;
  vertical-align: middle;
  .right {
    min-width: 400px;
    top: 50%;
    left: 100%;
    margin-left: 8px;
    transform: translate(0, -50%);
    padding: 10px 20px;
    color: #444444;
    background-color: #EEEEEE;
    font-weight: normal;
    font-size: 13px;
    border-radius: 8px;
    position: absolute;
    z-index: 99999999;
    box-sizing: border-box;
    box-shadow: 0 1px 8px rgba(0, 0, 0, 0.5);
    display: none;
    &:hover {
      display: block;
    }
    i {
      position: absolute;
      top: 50%;
      right: 100%;
      margin-top: -12px;
      width: 12px;
      height: 24px;
      overflow: hidden;
      &:after {
        content: '';
        position: absolute;
        width: 12px;
        height: 12px;
        left: 0;
        top: 50%;
        transform: translate(50%, -50%) rotate(-45deg);
        background-color: #EEEEEE;
        box-shadow: 0 1px 8px rgba(0, 0, 0, 0.5);
      }
    }
  }
  &:hover .right {
    display: block;
  }
}
